:host ::ng-deep {
  .p-steps .p-steps-item {
    &.step-completed .p-menuitem-link .p-steps-number {
      @apply bg-green-500;
    }

    &.step-error .p-menuitem-link .p-steps-number {
      @apply bg-red-500;
    }

    &.step-active .p-menuitem-link .p-steps-number {
      @apply bg-primary;
    }
  }

  .stepper-numbered {
    .p-steps-item:before {
      @apply hidden;
    }
  }
}
